html {
  --heading-size: 25px;
  --heading-base-width: 20rem;
  --font-icon-size: 72px;
  --font-greeting-size: 5.5rem;
  --font-name-size: 2rem;
  --font-status-size: 1.25rem;
  --img-size: 15rem;
}

.section-white-banner {
  height: 20vh;
}

.section-header {
  height: 80vh;
  width: 100%;
  background-color: #154155;
  color: #FCFFF5;
  display: grid;
  place-content: center;
  grid-template-rows: 20vh 40vh 20vh;
}

.header-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: center;
      align-self: center;
}

.header-contact{
  color: #D1DBBD;
  /* position: absolute; */

  grid-column: 1 / -1;
  grid-row: 3;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  height: 10rem;
}

.remove-default {
  text-decoration: none;
  color: #D1DBBD;
}

.header-contact-icon {
  font-size: var(--font-icon-size);
}

.highlight {
  text-decoration: none;
  display: inline;
  color: #801247;
  background-color: #ecd018;
  padding: 6px;
  border-radius: 4px;
}

.highlight-word {
  color: #D1DBBD;
}

.header-photo {
  width: 100%;
  height: 100%;
  -webkit-clip-path: circle(40% at 50% 50%);
          clip-path: circle(40% at 50% 50%);
  -o-object-fit: cover;
     object-fit: cover;
}

.section-white-banner {
  background-color: #3E606F;
  border-bottom: 5px solid var(--highlight-border-color);
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #4876EB;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.header-img {
  width: var(--img-size);
  height: var(--img-size);
  margin: 0 auto;
}
.home-photo-img {
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-transform: translateY(-115px) scale(1.1);
      -ms-transform: translateY(-115px) scale(1.1);
          transform: translateY(-115px) scale(1.1);
  border-radius: 16rem;
  border: 5px solid #D1DBBD;
}

.header-greating {
  font-size: var(--font-greeting-size);
  font-weight: 300;
  line-height: 1.2;
}

.header-name {
  font-size: var(--font-name-size);
}

.header-status {
  font-size: var(--font-status-size)
}

@media only screen and (max-width: 600px) {
  html {
    --heading-size: 25px;
    --heading-base-width: 20rem;
    --font-icon-size: 48px;
    --font-greeting-size: 2.2rem;
    --font-name-size: 1.25rem;
    --font-status-size: 1rem;
    --img-size: 10rem;
  }
  .home-photo-img {
     -webkit-transform: translateY(-90px) scale(1.2);
         -ms-transform: translateY(-90px) scale(1.2);
             transform: translateY(-90px) scale(1.2);
  }
  .highlight {
    padding: .4rem;
  }
}



